import routes from 'Routes.core';
import fs from 'fs-extra';
import path from 'path';
import renderer from './renderer';

const processPath = processee =>
  processee.startsWith('/') ? processee.slice(1) : processee;

export default Promise.all(
  routes
    .map(x => [renderer({ url: x.path }), processPath(x.path)])
    .map(([content, route]) =>
      fs.writeFile(
        path.resolve(
          process.env.SHINOBI_PUBLIC_DIR,
          `${route.length === 0 ? 'index' : route}.html`,
        ),
        content,
      ),
    ),
);
