import React from 'react';
import { renderToString } from 'react-dom/server';
import App from 'App.core';
import routes from 'Routes.core';
import Helmet from 'helmet';
import { StaticRouter, Switch, Route } from 'react-router-dom';

const assets = require(process.env.SHINOBI_ASSETS_MANIFEST);
const manifest = require(process.env.REGIME_CLIENT_MANIFEST);

const renderToHtml = markup => {
  const helmet = Helmet.renderStatic();
  return `<!doctype html>
    <html ${helmet.htmlAttributes.toString()}>
      <head>
      ${helmet.title.toString()}
      ${helmet.meta.toString()}
      ${helmet.link.toString()}
      <link rel="icon" href="favicon.ico">
        ${
          assets.client.css
            ? `<link rel="stylesheet" href="${assets.client.css}">`
            : ''
        }
        ${
          process.env.NODE_ENV === 'production'
            ? `<script src="${assets.client.js}" defer></script>`
            : `<script src="${assets.client.js}" defer crossorigin></script>`
        }
        <link rel="manifest" href="manifest.json">
        <meta name="theme-color" content="${manifest.theme_color}"/>
      </head>
      <body ${helmet.bodyAttributes.toString()}>
        <div id="root">${markup}</div>
      </body>
    </html>`;
};

export default ({ url }) =>
  renderToHtml(
    renderToString(
      <StaticRouter location={url} context={{}}>
        <App>
          <Switch>
            {routes.map((x, i) =>
              // eslint-disable-next-line react/no-array-index-key
              x.static ? <Route {...x} key={`${i}route`} /> : null,
            )}
          </Switch>
        </App>
      </StaticRouter>,
    ),
  );
